:root{
  --primary:#C1116A; --primary-600:#A30E5B; --accent:#E63B57;
  --text:#111317; --muted:#6b7280; --border:#e5e7eb;
  --surface:#fff; --bg:#f5f6f8; --success:#16a34a; --danger:#dc2626;
}

.parcels, .parcels * { box-sizing: border-box; }

/* страница */
.parcels{
  min-height:100dvh;
  display:grid;
  grid-template-rows: auto;
  background:var(--bg);
  color:var(--text);
  font-size:14px;
  line-height:1.35;
  padding-top:6px;
}
@supports (padding: max(0px)){ .parcels{ padding-top:max(env(safe-area-inset-top),6px); } }

/* контейнер контента */
.parcels__card{
  width:100%;
  max-width:420px;
  padding:8px 16px 16px;
  justify-self:center;
  display:grid;
  gap:18px;
  align-content:start;
}

/* логотип — ровно 66px */
.parcels__logo{
  display:flex;
  align-items:center;
  justify-content:flex-start;
  margin:0 0 4px;
  line-height:0;
}
.parcels__logo img{
  height:66px;
  display:block;
  object-fit:contain;
}

/* карточка формы */
.parcels__block{
  background:var(--surface);
  border:1px solid var(--border);
  border-radius:12px;
  padding:10px 12px;
  display:grid;
  gap:12px;
}
.parcels__block-title{ font-weight:700; font-size:14px; }

/* поля */
.parcels__inputs{ display:grid; gap:8px; }
.parcels__track-row{ position:relative; }
.parcels__input{
  width:100%;
  height:48px;
  border-radius:12px;
  outline:none;
  border:1px solid var(--border);
  background:var(--surface);
  padding:0 12px;
}
.parcels__input:focus{ border-color:var(--primary); box-shadow:0 0 0 3px rgba(193,17,106,.12); }

/* подсказки */
.parcels__menu{
  position:absolute; left:0; right:0; top:calc(100% + 6px); z-index:20;
  background:var(--surface); border:1px solid var(--border);
  border-radius:12px; box-shadow:0 12px 24px rgba(0,0,0,.12);
}
.parcels__list{ max-height:220px; overflow:auto; }
.parcels__option{ padding:10px 12px; cursor:pointer; }
.parcels__option:hover{ background:#fafafa; }
.parcels__empty{ padding:12px; color:var(--muted); }

/* кнопка */
.parcels__btn{
  width:100%; height:44px;
  border:0; border-radius:10px;
  background:var(--primary); color:#fff; font-weight:700; cursor:pointer;
}
.parcels__btn:active{ background:var(--primary-600); }
.parcels__success{ background:#ecfdf5; border:1px solid #bbf7d0; color:var(--success); padding:10px 12px; border-radius:10px; font-size:13px; }
.parcels__error{ color:var(--danger); font-size:12px; }

/* подсказка под кнопкой */
.parcels__hint{
  color: var(--muted);
  font-size: 12px;
}

/* сетка стадий */
.parcels__grid{ display:grid; grid-template-columns:1fr 1fr; gap:10px; }
.parcels__stage{
  position:relative;
  background:var(--surface);
  border:1.5px solid var(--primary);
  border-radius:16px;
  padding:12px;
  min-height:64px;
  display:grid;
  align-content:center;
  text-align:left;
  cursor:pointer;
}
.parcels__stage:active{ transform:translateY(1px); }
.parcels__stage-label{ font-size:13px; }
.parcels__badge{
  position:absolute; top:-9px; right:-9px;
  display:grid; place-items:center;
  min-width:22px; height:22px; padding:0 6px;
  background:#fff; border:2px solid var(--accent); color:var(--accent);
  border-radius:999px; font-size:12px; font-weight:700;
}

/* результаты */
.parcels__results{
  background:var(--surface); border:1px solid var(--border);
  border-radius:12px; padding:12px; display:grid; gap:8px;
}
.parcels__results-title{ font-weight:700; }
.parcels__bucket{ display:grid; gap:6px; }
.parcels__bucket-title{ color:var(--muted); font-size:12px; }
.parcels__bucket-list{ display:flex; flex-wrap:wrap; gap:6px; }
.parcels__tag{ border:1px solid var(--border); border-radius:999px; padding:4px 8px; font-size:12px; background:#fafafa; }

/* модалка */
.modal{ position:fixed; inset:0; z-index:50; }
.modal__backdrop{ position:absolute; inset:0; background:rgba(0,0,0,.35); }
.modal__dialog{
  position:absolute; left:50%; top:50%; transform:translate(-50%,-50%);
  width:calc(100vw - 24px); max-width:520px; max-height:84vh;
  background:#fff; border-radius:14px; border:1px solid var(--border);
  display:grid; grid-template-rows:auto 1fr; overflow:hidden;
}
.modal__header{ display:grid; grid-template-columns:1fr auto; align-items:center; padding:10px 12px; border-bottom:1px solid var(--border); }
.modal__title{ font-weight:700; }
.modal__close{ width:32px; height:32px; border:0; background:transparent; cursor:pointer; font-size:22px; line-height:1; color:var(--muted); }
.modal__body{ overflow:auto; padding:12px; }
.modal__muted{ color:var(--muted); }
.modal__error{ color:var(--danger); }
.modal__list{ display:grid; gap:10px; }
.parcelcard{ border:1px solid var(--border); border-radius:12px; padding:10px 12px; background:#fff; }
.parcelcard__row{ display:grid; grid-template-columns:120px 1fr; gap:8px; padding:3px 0; }

@media (max-width:360px){
  .parcels__input{ height:46px; }
  .parcelcard__row{ grid-template-columns:100px 1fr; }
}
