:root{
  --primary:#C1116A; --primary-600:#A30E5B; --accent:#E63B57;
  --text:#111317; --muted:#6b7280; --border:#e5e7eb;
  --surface:#fff; --bg:#f5f6f8; --success:#16a34a; --danger:#dc2626;

  /* Шрифты */
  --ff-body: "Roboto", system-ui, -apple-system, "Segoe UI", Arial, sans-serif; /* основной текст */
  --ff-ui: Arial, "Helvetica Neue", Helvetica, sans-serif;                      /* лейблы/кнопки */
}

.parcels, .parcels * { box-sizing: border-box; }

/* страница */
.parcels{
  min-height:100dvh;
  display:grid;
  grid-template-rows:auto;
  background:var(--bg);
  color:var(--text);

  /* как на макете: Roboto 16px */
  font-family:var(--ff-body);
  font-size:16px;
  line-height:1.35;

  padding-top:6px;
}
@supports (padding: max(0px)){ .parcels{ padding-top:max(env(safe-area-inset-top),6px); } }

/* контейнер */
.parcels__card{
  width:100%;
  max-width:420px;
  padding:8px 16px 16px;
  justify-self:center;
  display:grid;
  gap:18px;
  align-content:start;
}

/* логотип — 66px */
.parcels__logo{
  display:flex;
  align-items:center;
  justify-content:flex-start;
  margin:0 0 4px;
  line-height:0;
}
.parcels__logo img{
  height:66px;
  display:block;
  object-fit:contain;
}

/* карточка формы */
.parcels__block{
  background:var(--surface);
  border:1px solid var(--border);
  border-radius:12px;
  padding:10px 12px;
  display:grid;
  gap:12px;
}
.parcels__block-title{
  font-family:var(--ff-ui);   /* Arial */
  font-weight:700;
  font-size:16px;
}

/* поля */
.parcels__inputs{ display:grid; gap:8px; }
.parcels__track-row{ position:relative; }
.parcels__input{
  width:100%;
  height:48px;
  border-radius:12px;
  outline:none;
  border:1px solid var(--border);
  background:var(--surface);
  padding:0 12px;

  font-family:var(--ff-body); /* Roboto */
  font-size:16px;
}
.parcels__input::placeholder{ color:#9ca3af; }
.parcels__input:focus{ border-color:var(--primary); box-shadow:0 0 0 3px rgba(193,17,106,.12); }
.parcels__input.is-invalid{ border-color:var(--danger) !important; box-shadow:0 0 0 3px rgba(220,38,38,.12); }

/* подсказки */
.parcels__menu{
  position:absolute; left:0; right:0; top:calc(100% + 6px); z-index:20;
  background:var(--surface); border:1px solid var(--border);
  border-radius:12px; box-shadow:0 12px 24px rgba(0,0,0,.12);
}
.parcels__list{ max-height:220px; overflow:auto; }
.parcels__option{
  padding:10px 12px; cursor:pointer;
  font-family:var(--ff-body); /* Roboto */
  font-size:16px;
}
.parcels__option:hover{ background:#fafafa; }
.parcels__empty{ padding:12px; color:var(--muted); }

/* кнопка */
.parcels__btn{
  width:100%; height:44px;
  border:0; border-radius:10px;
  background:var(--primary); color:#fff; cursor:pointer;

  font-family:var(--ff-ui);   /* Arial */
  font-weight:700;
  letter-spacing:.2px;
}
.parcels__btn:active{ background:var(--primary-600); }

/* подсказка/ошибка */
.parcels__hint{
  color:var(--muted);
  font-size:12px;
  font-family:var(--ff-body); /* Roboto */
}
.parcels__error{
  color:var(--danger);
  font-size:12px;
  font-family:var(--ff-body); /* Roboto */
}
.parcels__success{
  background:#ecfdf5; border:1px solid #bbf7d0; color:var(--success);
  padding:10px 12px; border-radius:10px; font-size:13px;
  font-family:var(--ff-body); /* Roboto */
}

/* блок результата (created_event) */
.parcels__results{
  background:var(--surface); border:1px solid var(--border);
  border-radius:12px; padding:12px; display:grid; gap:8px;
}
.parcels__results-title{
  font-family:var(--ff-ui);   /* Arial */
  font-weight:700;
  font-size:16px;
}
.parcels__bucket{ display:grid; gap:6px; }
.parcels__bucket-list{ display:flex; flex-wrap:wrap; gap:6px; }
.parcels__tag{
  border:1px solid var(--border);
  border-radius:999px;
  padding:4px 8px;
  font-size:12px;
  background:#fafafa;
  font-family:var(--ff-body); /* Roboto */
}

@media (max-width:360px){
  .parcels__input{ height:46px; }
}
