:root{
  --primary:#C1116A;
  --text:#111317;
  --muted:#6b7280;
  --tab-bg:#f7eaf2;
  --tab-border:#e7c0d2;
}

.btabs{
  position: fixed;
  left: 0; right: 0; bottom: 0;
  height: 64px;
  padding-bottom: env(safe-area-inset-bottom, 0);
  background: var(--tab-bg);
  border-top: 1px solid var(--tab-border);
  display: grid;
  grid-template-columns: repeat(3,1fr);
  z-index: 100;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

.btabs__item{
  position: relative;
  display: grid; place-items: center;
  gap: 4px;
  text-decoration: none;
  color: var(--muted);
  user-select: none;
}
.btabs__item.is-active{ color: var(--primary); font-weight: 600; }

.btabs__icon{
  width: 22px; height: 22px;
  object-fit: contain;
  pointer-events: none;
}

.btabs__text{ font-size: 12px; line-height: 1; }

.btabs__indicator{
  position: absolute;
  top: -1px; left: 25%; right: 25%;
  height: 4px; border-radius: 4px 4px 0 0;
  background: var(--primary);
}

/* 320–360 */
@media (max-width: 360px){
  .btabs{ height: 60px; }
  .btabs__icon{ width: 20px; height: 20px; }
  .btabs__text{ font-size: 11px; }
}
