:root{
  --primary:#C1116A; --primary-600:#A30E5B; --text:#111317; --muted:#6b7280;
  --border:#e5e7eb; --surface:#fff; --bg:#f5f6f8; --danger:#dc2626; --success:#16a34a;

  /* Шрифты */
  --ff-body: "Roboto", system-ui, -apple-system, "Segoe UI", Arial, sans-serif; /* как на 2-м скрине */
  --ff-ui: Arial, "Helvetica Neue", Helvetica, sans-serif;                      /* как на 1-м скрине */
}

.home, .home * { box-sizing: border-box; }

/* Страница */
.home{
  min-height: 100%;
  background: var(--bg);
  color: var(--text);

  /* База как на инспекторе: Roboto 16px */
  font-family: var(--ff-body);
  font-size: 16px;
  line-height: 1.35;
}

/* Карточка контента */
.home__card{
  width: 100%;
  max-width: 420px;
  margin: 0 auto;
  padding: 16px;
  display: grid;
  gap: 12px;
  position: relative;
}

/* Шапка карточки: код слева, логотип справа */
.home__head{
  display:grid;
  grid-template-columns: 1fr auto;
  align-items: start;
  gap: 12px;
}

/* Логотип в правом верхнем углу карточки */
.home__brand{
  height: 55px;
  object-fit: contain;
  display: block;
  transform: translateY(-2px);
}
@media (max-width: 360px){
  .home__brand{ height: 38px; }
}

/* Код клиента */
.home__sub{
  font-family: var(--ff-ui);   /* Arial */
  font-size:12px;
  color:var(--muted);
  margin-bottom:6px;
}
.home__chip{
  height: 36px;
  display: inline-grid; grid-auto-flow: column; align-items:center;
  gap: 8px; padding: 0 8px 0 10px;
  background: var(--surface); border: 1px solid #e7c0d2; border-radius: 10px;
  font-family: var(--ff-ui);   /* Arial */
}
.home__chip-text{ font-weight:600; letter-spacing: .2px; }
.home__chip-btn{ width:28px; height:28px; display:grid; place-items:center; border:0; background:transparent; color:var(--muted); cursor:pointer; }

/* Поиск */
.home__label{
  font-family: var(--ff-ui);   /* Arial */
  font-weight:700;
}
.home__track-wrap{ position: relative; }
.home__input{
  width:100%; height:44px; padding:0 44px 0 12px;
  border:1px solid #e7c0d2; border-radius:10px; background:var(--surface); outline:none;

  /* Поля пусть остаются в Roboto как основной текст */
  font-family: var(--ff-body);
  font-size: 16px;
}
.home__input::placeholder{ color:#9ca3af; }
.home__input:focus{ border-color:var(--primary); box-shadow:0 0 0 3px rgba(193,17,106,.12); }
.home__search-ico{
  position:absolute; right:6px; top:50%; transform:translateY(-50%);
  width:32px; height:32px; display:grid; place-items:center; border:0; background:transparent; color:var(--muted); cursor:pointer;
}
.home__search-ico:hover{ color:var(--text); }

/* Подсказки */
.home__menu{
  position:absolute; left:0; right:0; top:calc(100% + 6px); z-index:20;
  background:var(--surface); border:1px solid var(--border); border-radius:12px; box-shadow:0 12px 24px rgba(0,0,0,.12);
}
.home__list{ max-height:220px; overflow:auto; }
.home__option{ padding:10px 12px; cursor:pointer; }
.home__option:hover{ background:#fafafa; }
.home__empty{ padding:12px; color:var(--muted); }

/* Адрес */
.home__addr{ background:var(--surface); border:1px solid var(--border); border-radius:12px; overflow:hidden; }

/* Строки адреса: ключи — Arial, значения — Roboto */
.home__row{
  display:grid; grid-template-columns: 1fr 1.2fr; gap:8px; padding:10px 12px; border-top:1px solid var(--border);
  font-size:16px; line-height:1.35;   /* соответствует инспектору */
}
.home__row:first-child{ border-top:0; }
.home__row-key{
  font-family: var(--ff-ui);   /* Arial как на 1-м скрине */
  color:var(--text);
  font-weight:600;
}
.home__row-val{
  font-family: var(--ff-body); /* Roboto как на 2-м скрине */
  color:#6b7280;
}

/* Кнопка */
.home__copy{
  width:100%; height:46px; border:0; border-radius:12px;
  background:var(--primary); color:#fff; font-weight:700; cursor:pointer;

  font-family: var(--ff-ui);   /* Arial */
  letter-spacing:.2px;
}
.home__copy:active{ background:var(--primary-600); }

/* Лоадер */
.home__loading{ color:var(--muted); text-align:center; }

/* Мелкий адаптив */
@media (max-width: 360px){
  .home__input{ height:46px; }
  .home__copy{ height:44px; }
}

/* хинт и ошибка под инпутом */
.home__hint{
  color: var(--muted);
  font-size: 12px;
  margin-top: 6px;
  font-family: var(--ff-body); /* Roboto */
}
.home__error{
  color: var(--danger);
  font-size: 12px;
  margin-top: 4px;
  font-family: var(--ff-body); /* Roboto */
}

/* подсветка ошибки поля */
.home__input.is-invalid{
  border-color: var(--danger) !important;
  box-shadow: 0 0 0 3px rgba(220, 38, 38, .12);
}
